<template>
    <div class="main">
      <div class="jinrishici-sentence">
        <span id="jinrishici-sentence">每日诗词...</span>
      </div>
      <canvas id="canvas" width="auto"/>   
      <button class="infomsg" @click="$router.push({path:'/info'})" style="cursor:pointer;"><h5>Moument Of Version</h5></button><!-- 绑定点击按钮 -->
      <transition name="fade">
        <div class="search-input" v-show="isShow">
          <el-autocomplete
              v-model="input"
              :fetch-suggestions="querySearchAsync"
              @select="toSearch"
              :trigger-on-focus="false"
              class="input-text"
              :autofocus="true"
              :input-style="{'font-size':'17px'}"
              @keydown.down="add(1)"
              @keydown.up="add(-1)"
              @keydown.enter="toSearch"
              popper-class="select-option"
          >
            <template #prefix>
              <el-tooltip content="点击切换搜索引擎" placement="bottom" effect="light" :show-after="500">
                <search-engines-switch></search-engines-switch>
              </el-tooltip>
            </template>
            <template #suffix>
              <button class="start-search" @click="toSearch" style="cursor:pointer;">Start&nbsp;&nbsp;</button>
            </template>
          </el-autocomplete>
        </div>
        </transition>
        <tools></tools>
    </div>
  </template>
  
  <script>
import { init } from './rain';
import {ref,onMounted} from 'vue'
import { jsonp } from 'vue-jsonp'
import {useStore} from 'vuex'
import SearchEnginesSwitch from "@/components/SearchEnginesSwitch";
import Tools from './Tools.vue';
export default {
  name: "Home",
  components:{
    SearchEnginesSwitch,
    Tools
  },
  data() {
    return {
      canvasStyle: {
        width: "100%",
        height: "100%",
        zIndex: "-1",
        left: "0",
        bottom: "0",
      },
      ctx: {},
    };
  },
  mounted() {
    this.initCanvas();
  },
  methods: {
    initCanvas() {
      const canvas = document.querySelector("#canvas");
      this.ctx = canvas.getContext("2d");
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
      init(this.ctx);
    },
  },
  setup(){
    const store = useStore();

    let input = ref("");
    let isShow = ref(!store.state.enterAnimation);

    let lastFetch = [];
    let backup = "";
    let move = -1;

    const querySearchAsync = (queryString, cb) => {
      let results = [];
      lastFetch = [];
      move = -1;
      backup = input.value;
      jsonp('https://suggestion.baidu.com/su?wd='+queryString,{callbackQuery: 'cb'}).then(res=>{
        res.s.forEach(item => results.push({value: item}));
        cb(results);
        lastFetch = results;
        move = -1;
      })
    };

    const toSearch = () => {
      window.location.href = store.getters.currentSearchEngine.url + input.value
    }

    onMounted(()=>{
      setTimeout(()=>{
        isShow.value = true;
      },1)
    })

    const add = (num)=>{
      move += num;
      if(move > lastFetch.length-1){
        move = lastFetch.length-1;
      }

      if(move < 0){
        move = -1;
        input.value = backup;
      }else{
        input.value = lastFetch[move].value;
      }
    }

    return {
      input,
      querySearchAsync,
      toSearch,
      isShow,
      add
    }
  }
}
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style lang="scss">
  .main {
    height: 300px;
    width: 100%;
    padding: 10px 0 0 0;
    background-image: linear-gradient(to bottom right, #63bbf7, #5289fc, #9c83f7);
    p{
      margin: 0 0 0 25px;
      color: rgb(238, 238, 238);
      font-weight: 400;
      font-size: 20px;
      font-family: sans-serif;
      font-style: italic;
    }  
    .jinrishici-sentence {
      position: absolute;
      left: 50%;
      top: 100px;
      transform: translateX(-50%);
      height: 20px;
      font-size: 23px;
      color: #ffffff;
    }
    .infomsg{
      position: absolute;
      top: 5px;
      right: 25px;
      font-size: 25px;
      background-color: #ffffff00;
      /*取消四周黑边*/
      border: none;
      outline: none;
      h5{
        color: #ffffff;
      }
    }
  }
  .select-option {
    background-color: #ffffff00 !important;
    border: none !important;
    outline: none !important;
    li{
      color: #000000 !important;
      font-weight: 500 !important;
    }
  }

  .el-input__inner{
    border-radius: 5px !important;
    /*-webkit-box-shadow: #ccc 0 0 15px;
    -moz-box-shadow: #ccc 0 0 15px;
    box-shadow: #ccc 0 0 15px;*/
    background-color: #ffffff00 !important;
  }
  .input-text{
    width: 600px;
    max-width: 1200px;
    min-width: 200px;

  }
  .start-search{
    color: black;
    font-size: 16px;
    font-weight: 700;
    margin: 0 0 0 0;
    border: 0;
    background: transparent;
    height: 100%;
  }

  .el-input--prefix .el-input__inner{
    padding-left: 50px !important;
  }
  .el-input--suffix .el-input__inner{
    padding-right: 50px !important;
  }
  .banner{
    margin: 15px;
  }
  .search-input{
    position: absolute;
    top: 180px;
    left: 50%;
    transform: translateX(-50%);
    margin-bottom: 50px;
    
  }
  .fade-enter-active{
    transition: opacity 0.8s linear;
  }
  .fade-enter-from{
    opacity: 0;
  }

  .slide-up-enter-active {
    transition: all 0.6s ease-out;
  }
  .slide-up-enter-from{
    transform: translateY(20px);
  }
  .footer-info{
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 1px;
    color: #dddddd;
  }
  </style>