<template>
    <div class="result-list">
        <div class="index-group-boxIn" ref="groupBoxRef">
            <div v-for="item in groupInfo" :key="item.id" ref="groupCardRef" class="group-card" >
                <a target="_blank" :href="item.url">
                    <div class="box">
                    <img :src="item.img" alt="">
                    </div>
                </a>
                <div class="title-box">{{item.title}}</div>
            </div>
            
        </div>
    </div>
</template>

<script>
import { defineComponent, ref, reactive, onMounted, watchEffect } from 'vue';
export default defineComponent({
    name: 'scroll',
    setup() {
        const groupInfo = ref([]); // 卡片list
        const direction = ref('right'); // 默认箭头向右
        const arrowShow = ref(false); // 滚动箭头是否显示
        const groupBoxRef = ref(null); // 获取外层卡⽚ref
        const groupCardRef = ref(null); // 获取卡⽚ref
        const scrollPosition = reactive({
            left: 0,
            top: 0
        }); // 滚动位置

  
        // 获取卡片列表
        const getMyGroup = async () => {
            const data = [{
                id: 1,
                title:'本地路由页面',
                url: 'http://192.168.8.1/cgi-bin/luci/',
                img: 'https://s2.loli.net/2022/11/20/VWuinomOaUKzQAp.png'
            },{
                id: 2,
                title:'阿虚同学的储物间',
                url: 'https://axutongxue.com/',
                img: 'https://lh1.hetaousercontent.com/img/cd01e235b471f9b2.png?thumbnail=true'
            },{
                id: 3,
                title:'MSDN',
                url: 'https://msdn.itellyou.cn/',
                img: 'https://static.itellyou.cn/images/logo.png'
            },{
                id: 4,
                title:'下歌吧',
                url: 'https://music.y444.cn/#/',
                img: 'https://static.music.y444.cn/static/img/logo.397ed8f7.png'
            },{
                id: 5,
                title:'程序员工具箱',
                url: 'https://tool.lu/',
                img: 'https://s2.loli.net/2022/11/21/t2VvZJdoExOfMAq.png'
            },{
                id: 6,
                title:'chrome镜像',
                url: 'https://www.extfans.com/',
                img: 'https://s2.loli.net/2022/11/20/ThPkIBvaotbNWrZ.png'
            },{
                id: 7,
                title:'SM.MS图床',
                url: 'https://sm.ms/',
                img: 'https://s2.loli.net/2022/11/20/ine5ENOhLbQ7DIy.png'
            },{
                id: 8,
                title:'PDF转换',
                url: 'https://www.pdf2go.com/zh',
                img: 'https://www.pdf2go.com/assets/logo/logo_pdf2go.svg'
            },{
                id: 9,
                title:'微软激活码',
                url: 'https://www.nruan.com/win-key.html/',
                img: 'https://s2.loli.net/2022/11/20/akHidfP19TCImuF.png'
            },{
                id: 10,
                title:'ipv6测试',
                url: 'https://test-ipv6.com/',
                img: 'https://s2.loli.net/2022/11/21/fQH9pv5uLcUqNSd.png'
            },{
                id: 11,
                title:'转换favicon图标',
                url: 'https://www.bitbug.net/',
                img: 'https://s2.loli.net/2022/11/25/teY2DnUdGXiNW7p.png'
            }]
            groupInfo.value = data;
        }
    
        // 获取卡⽚宽度，第⼀个参数是卡⽚个数，默认是整个数组，第⼆个参数是剩余的margin
        const getWidth = (num = groupInfo.value.length, restMargin = 16) => {
            // 如果没有内容就返回0
            if(!groupCardRef.value) return 0;
            return num * (groupCardRef.value.offsetWidth + 16) - restMargin;
        }
        // 改变滚动⽅向
        const changeArrow = (scrollLeft) => {
            // 默认获取scoll部分整个宽度
            const getScrollWidth = getWidth();
            // 获取剩余宽度
            const restWidth = getScrollWidth - scrollLeft
            if (restWidth <= groupBoxRef.value.offsetWidth) {
                direction.value = 'left'
            } else if ( scrollLeft === 0 ) {
                direction.value = 'right'
            }
        }
        // ⿏标点击滚动
        const groupScroll = async () => {
            // 获取滚动宽度
            const getMoveWidth = getWidth(3, 0);
            if (direction.value === 'right') {
                groupBoxRef.value.scrollLeft += getMoveWidth;
            } else {
                groupBoxRef.value.scrollLeft -= getMoveWidth;
            }
            // 滚动需要时间才能获取最新的距离
            setTimeout(() => {
                changeArrow(groupBoxRef.value.scrollLeft);
            }, 500)
        }

        // 判断arrow是否展示
        const checkArrowShow = () => {
            arrowShow.value = getWidth() > groupBoxRef.value?.offsetWidth ? true : false;
        }

        watchEffect(() => {
            checkArrowShow();
        })

        // 获取scroll函数的位置
        const handleScroll = e => {
            scrollPosition.left = e.target.scrollLeft;
            scrollPosition.top = e.target.scrollTop;
            changeArrow(scrollPosition.left);
        }

        getMyGroup();

        onMounted(() => {
            // 监听scroll事件
            groupBoxRef.value.addEventListener('scroll', handleScroll, true);
            // 监听窗⼝变化事件，判断arrow的展示
            window.addEventListener('resize', checkArrowShow, true);
            // 首次检查箭头展示
            checkArrowShow();
        })

        return {
            // data
            groupInfo,
            direction,
            arrowShow,
            // ref
            groupBoxRef,
            groupCardRef,
            // methods
            groupScroll
        };
    },
});
</script>

<style lang="scss" >
.index-group-box {
    padding-right: 16px;
    position: relative;
    box-sizing: border-box;
    width: 100%;
}  

.scrollX {
    width: 16px;
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background-color: #512D6D;
    display: flex;
    justify-content: center;
    align-items: center
}

.scrollX:hover {
    cursor: pointer;
    background-color: #65447d;
}

.index-group-boxIn {
    display: flex;
    scroll-behavior: smooth;
    white-space: nowrap;
    overflow-x: auto;
    flex: none;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */
}

.index-group-boxIn::-webkit-scrollbar {
    display: none; /* Chrome Safari */
}

.group-card {
    display: inline-block;
    padding: 12px 2px;
    box-sizing:border-box;
    width: 130px;
    height: 100px;
    border-radius: 4px;
    margin: 2% 30px 0 0;
    flex: none;
    border-radius: 10px 10px 10px 10px;
    .title-box {
        margin: 0px 0 0 0px;
        font-weight: 400;
        font-size: 13px;
    }
}

.group-card span{
    color: #0a0a0a;
}

.group-card:hover{
    background: #eaebeab0;
}

.group-card:nth-last-of-type(1){
    margin-right: 0px;
}
.result-list {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateY(-30%);
    transform: translateX(-50%);
    width: 85%;
    height: 800px;
    /*margin: 50px 100px 50px 100px;
    padding: 20px 20px 20px 20px;*/
    transform: translateX(-50%);
    border-radius: 15px;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 1px 1px 1px rgba(131, 131, 131, 0.623);
    .box {
        /*padding: 50px 0 0 35px;*/    
        img {
            margin: 0 0 0 20%;
            padding: 0 0 8px 0;
            width: 52px;
            height: 52px;
            border-radius: 50px;
        }               
    }
}


.box:hover{
    transform: translateY(-3px);
}
.title-box:hover{
    
}
.article-title {
    position: absolute;
    font-size: 10px;
    padding: 10px 70% 0 0;
    font-weight: 600;
    line-height: 1.5;
    color: #252933;
}

</style>